











































































import {
  format,
  parse,
  addMonths,
  subMonths,
  startOfYear,
  addDays,
  subDays
} from "date-fns";
import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import { settingsModule } from "@/store/modules";
import { Language } from "@/shared";
import { defaultRepeatPattern, cronExecutionDatesBetween } from "@/misc/cron";
import objectHash from "object-hash";

import memoize from "lodash.memoize";

/**
 * Given as prop
 */
export interface ICalendarEvent {
  title: string;
  details?: string;
  date: Date;
  class: string;
}

/**
 * Actual event rendered
 */
interface IInternalCalendarEvent {
  title: string;
  details: string;
  date: string;
  class: string;
}

export const dateToYYYYMMDDString = (d: Date) => format(d, "YYYY-MM-DD");
const dateToHHMMString = (d: Date) => format(d, "HH:mm");

const classes = [
  "red",
  "pink",
  "purple",
  "deep-purple",
  "indigo",
  "blue",
  "light-blue",
  "cyan",
  "teal",
  "green",
  "light-green",
  "amber",
  "orange",
  "deep-orange",
  "brown",
  "blue-grey"
].flatMap(c => [c, `darken-3 ${c}`]);

const getClassBasedOnName = memoize((name: string) => {
  let hash = 0;
  for (let i = 0; i < name.length; ++i) {
    // tslint:disable-next-line
    hash = (hash << 5) - hash + name.charCodeAt(i);
    // tslint:disable-next-line
    hash |= 0;
  }
  hash = Math.abs(hash);
  const c = classes[hash % classes.length];
  return c;
});

@Component({})
export default class JobExecutionCalendar extends Mixins() {
  @Prop({
    default: () => [],
    type: Array
  })
  jobs!: ({ name: string; repeatPattern: string, startDate: Date })[];

  @Prop({
    default: 50,
    type: Number
  })
  maxEventsPerJob!: number;

  get calendarEvents(): ICalendarEvent[] {
    return this.jobs.flatMap(job => {
      // always use first day of the month
      const currentPageParsed = parse(
        `${this.calendarStartDate.substr(0, 7)}-01`
      );
      let startDate = dateToYYYYMMDDString(subDays(currentPageParsed, 8));
      if (startDate < this.today) {
        startDate = this.today;
      }
      const jobStartDate = dateToYYYYMMDDString(job.startDate);
      if (startDate < jobStartDate) {
        startDate = jobStartDate;
      }
      const endDate = dateToYYYYMMDDString(addDays(currentPageParsed, 40));
      const dates = cronExecutionDatesBetween(
        job.repeatPattern,
        startDate,
        endDate,
        this.maxEventsPerJob
      );
      return dates.map(
        (d): ICalendarEvent => ({
          class: getClassBasedOnName(job.name),
          date: d,
          title: job.name,
          details: `${this.$t(
            "calendar.job-start"
          ).toString()}: ${dateToHHMMString(d)}`
        })
      );
    });
  }

  @Watch("calendarStartDate")
  handleNullDate() {
    this.calendarStartDate =
      (this.calendarStartDate as any).date ||
      this.calendarStartDate ||
      this.today;
  }

  calendarStartDate = "";

  mounted() {
    this.calendarStartDate = this.today;
  }
  get eventsMap() {
    const map: { [idx: string]: IInternalCalendarEvent[] | undefined } = {};
    this.calendarEvents.forEach(e => {
      const date = dateToYYYYMMDDString(e.date);
      (map[date] = map[date] || []).push({
        title: e.title,
        details: e.details || dateToHHMMString(e.date),
        date,
        class: e.class
      });
    });
    return map;
  }
  get today() {
    return dateToYYYYMMDDString(new Date());
  }
  resetCalendarStartDate() {
    this.calendarStartDate = this.today;
  }
  get resetCalendarStartDateDisabled() {
    // only compare year and month
    return (
      (this.calendarStartDate || "").substr(0, 7) === this.today.substr(0, 7)
    );
  }
  get locale() {
    return settingsModule.language;
  }
  get weekdayFormat() {
    return this.locale === Language.en
      ? [0, 1, 2, 3, 4, 5, 6] /* start of week = sunday */
      : [1, 2, 3, 4, 5, 6, 0] /* start of week = monday */;
  }
}
